<template>
    <form class="container">
        <h3 class="text-muted my-5">添加新的pizza</h3>
        <div class="form-group row">
            <label class="col-sm-1">品种</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.name">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">描述</label>
            <div class="col-sm-11">
                <textarea rows="5" class="form-control" v-model="newpizza.des"></textarea>
            </div>
        </div>
        <p><strong>选项1</strong></p>
        <div class="form-group row">
            <label class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.size1">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.price1">
            </div>
        </div>
        <p><strong>选项2</strong></p>
        <div class="form-group row">
            <label class="col-sm-1">尺寸</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.size2">
            </div>
        </div>
        <div class="form-group row">
            <label class="col-sm-1">价格</label>
            <div class="col-sm-11">
                <input type="text" class="form-control" v-model="newpizza.price2">
            </div>
        </div>
        <div class="form-group row">
            <button @click="addMenuItem()" class="btn btn-block btn-success">添加</button>
        </div>
    </form>
</template>
<script>
 export default{
    data(){
        return{
            newpizza:{
                name:"",
                des:"",
                size1:"",
                size2:"",
                price1:"",
                price2:""
            }
        }
    },
    methods:{
        addMenuItem(){
            let data = {
                name:this.newpizza.name,
                description:this.newpizza.des,
                option:[
                    {
                        size:this.newpizza.size1,
                        price:this.newpizza.price1
                    },
                     {
                        size:this.newpizza.size2,
                        price:this.newpizza.price2
                    }
                ]
            }
           fetch("https://wd5015835072eqzfab.wilddogio.com/menu.json",{
               method:"POST",
               headers:{
                   "Content-type":"application/json"
               },
               body:JSON.stringify(data)
           }) 
           .then(res => res.json())
        //    .then(data => this.$router.push({path:"/menu"}))
        // 数据同步到vuex
           .then(data => this.$store.commit("pushToMenuItem",data))
           .catch(err => console.log(err))
        }
    }
 }   
</script>
<style>
    
</style>


