<template>
    <div class="row">
        <!-- 菜单 -->
        <div class="col-sm-12 col-md-8">
           <table class="table">
               <thead class="thead-default">
                   <tr>
                       <th>尺寸</th>
                       <th>价格</th>
                       <th>加入</th>
                   </tr>
               </thead>
               <tbody v-for="item in getMenuItem">
                   <tr>
                       <td><strong>{{item.name}}</strong></td>
                   </tr>
                   <tr v-for="option in item.option">
                       <td>{{option.size}}</td>
                       <td>{{option.price}}</td>
                       <td><button v-on:click.prevent="addToBasket(item,option)"
                       class="btn btn-sm btn-outline-success">+</button></td>
                   </tr>
               </tbody>
           </table>
        </div>
        <!-- 购物车 -->
        <div class="col-sm-12 col-md-4">
            <div v-if="baskets.length > 0">
                <table class="table">
                <thead class="thead-default">
                   <tr>
                       <th>数量</th>
                       <th>品种</th>
                       <th>价格</th>
                   </tr>
                </thead>
                <tbody v-for="item in baskets">
                    <tr>
                        <td>
                            <button @click="decreaseQuantity(item)" class="btn btn-sm">-</button>
                            <span>{{item.quantity}}</span>
                            <button @click="addQuantity(item)" class="btn btn-sm">+</button>
                        </td>
                        <td>{{item.name}}{{item.size}}</td>
                        <td>{{item.price*item.quantity}}</td>
                    </tr>
                </tbody>
            </table>
            <p>总价：{{total}}RMB</p>
            <button class="btn btn-block btn-success">提交</button>
            </div>
            <div v-else>
                <p>购物车没有任何商品</p>
            </div>
        </div>
    </div>
       
</template>
<script>
export default{
    data(){
       return {
        //   getMenuItem:{},
          baskets:[],
       }
    },
    created(){
    //    fetch("https://wd5015835072eqzfab.wilddogio.com/menu.json")
    //    .then(res => {
    //        return res.json()
    //     })
    //     .then(data => {
    //     this.getMenuItem = data;

    //     })

    // 下面将请求下来的数据传到vuex
          fetch("https://wd5015835072eqzfab.wilddogio.com/menu.json")
          .then(res =>{
              return res.json()
          })
          .then(data =>{
             this.$store.commit("setMenuItem",data)
          })
    },
    computed:{
        // 在vuex中获取数据
        getMenuItem(){
            return this.$store.state.menuItem
        },
        total(){
            let tatolCost = 0;
            for(let key in this.baskets){
                tatolCost += this.baskets[key].quantity * this.baskets[key].price; 
            }
            return tatolCost;
        }
    },
    methods:{
              addToBasket(item,option){
                    let basket = {
                      name:item.name,
                      size:option.size,
                      price:option.price,
                      quantity:1,
                    }
                    //  过滤
                    if(this.baskets.length > 0){
                        let result = this.baskets.filter((basket)=>{
                            return (basket.name === item.name && basket.price === option.price)
                        })
                        if(result != null && result.length >0){
                            result[0].quantity ++
                        }else{
                           this.baskets.push(basket)
                        }
                    }else{
                        this.baskets.push(basket)
                    }
                    
                    
                 
                  
              },
              decreaseQuantity(item){
                 item.quantity --;
                 if(item.quantity == 0){
                     this.removeBasket(item);
                 }
              },
              addQuantity(item){
                 item.quantity ++
              },
              removeBasket(item){
                  this.baskets.splice(this.baskets.indexOf(item),1)
              }
    },
    

}  

</script>
<style scoped>

</style>


