<template>
    <div class="row">
        <div class="col-sm-12 col-md-8">
            <!-- new pizza -->
            <newp></newp>
        </div>
        <div class="col-sm-12 col-md-4">
            <!-- 品种展示 -->
            <h3 class="text-muted my-5">菜单</h3>
            <table class="table">
                <thead class="table table-default">
                    <tr>
                        <th>品种</th>
                        <th>删除</th>
                    </tr>
                </thead>
                <tbody v-for="item in getMenuItems">
                    <tr>
                        <td>{{item.name}}</td>
                        <td>
                         <button @click="deletePizza(item)" class="btn btn-sm btn-danger">X</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
import NewPizza from "./NewPiza"
export default{
    data(){
        return {
        //    getMenuItems:[]
        }
    },
    components:{
        "newp":NewPizza
    },
    methods:{
        deletePizza(item){
            fetch("https://wd5015835072eqzfab.wilddogio.com/menu/"+item.id+".json",{
                method:"DELETE",
                headers:{"Content-type":"application/json"}
            })
            // .then(res => this.$router.push({path:"/menu"}))
            .then(data => {
                this.$router.commit("removeItem",item)
                location.reload();
                })
            .catch(err => console.log(err))
            location.reload();
        }
    },
    created(){
        fetch("https://wd5015835072eqzfab.wilddogio.com/menu.json")
        .then(res =>{
            return res.json()
        })
        .then(data =>{
            let arr = [];
            for(let key in data){
                data[key].id = key;
                arr.push(data[key]);
                // this.getMenuItems.push(data[key]);
            }
            this.$store.commit("setMenuItem",arr)
            // console.log(this.getMenuItems)
        })
        .catch(err => console.log(err))
    },
    computed:{
        getMenuItems:{
           get(){
               return this.$store.state.menuItem
           },
           set(){

           }
        }
    },
}  

</script>
<style>

</style>


