import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

export const store = new Vuex.Store({
    state:{
      //设置属性
      menuItem:[],
      currentusers:null,
      islogin:false
    },
    getters:{
      //获取属性的状态  
       currentusers:(state) =>{
            return state.currentusers
         },
       islogin:(state) =>{
            return state.islogin
         }
    },
    mutations:{
      //改变属性的状态  
      setMenuItem(state,data){
         state.menuItem = data
      },
      removeItem(state,data){
         state.menuItem.forEach((menuItem,index)=> {
            if(data == menuItem){
               menuItem.splice(index,1)
            }
         });
      },
      pushToMenuItem(state,data){
         state.menuItem.push(data)
      },
      // action传过来的
      // userstatus(state,user){
      //    if(user){
      //       state.currentusers = user
      //       state.islogin = true
      //    }else{
      //       state.currentusers = null
      //       state.islogin = false
      //    }
      // }
      setuser(state,data){
         if(data){
            state.currentusers = data
            state.islogin = true
         }else{
            state.currentusers = null
            state.islogin = false
         }
      }
    },
    actions:{
      //应用metation
      // setuser({commit},user){
      //     commit("userstatus",user) 
      // }
    }
})


